
#app { width: 100%;
}
.tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
  position: relative;
}
.tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.tab .tab-item > a {
  display: block;
  font-size: 14px;
  color: #4d555d;
  text-decoration: none;
}
.tab .tab-item > a.active {
  color: rgb(240,20,20);
}
.border-1px {
}
.tab:after {
  content:"";
  border-bottom: 1px solid #ccc;
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
}



